

.single-news {
  &__image {
    float: left;
    margin-right: 15px;
    margin-bottom: 15px;
    clear: both;
  }
  &__text {
    padding: 30px 15px;
    background: $white-color;
  }
}

.news-list {
  &__wrapper {
    background: $white-color;
    display: flex;
    flex-wrap: wrap;
  }
}

.news-item {
  position: relative;
  width: 25%;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__link {
    text-decoration: none;
    &:hover {
      text-decoration: none;
      color: $green-color;
    }
  }
  &__body {
    text-align: justify;
  }
  &__footer {
    margin-top: 15px;
    text-align: right;
    color: lighten($black-color, 45%);
  }
}